
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import api from '@/api'
import utils from '@/utils'

export default {
name: 'DeviceRepairDetail',
directives: {
TransferDom
},
components: {
XHeader,
Swiper,
SwiperItem,
Rater,
ViewBox,
Confirm,
XButton,
Flexbox,
FlexboxItem
},
mounted () {
this.repair_id = window.location.href.split('/repair-detail/')[1]
let self = this

this.axios
.get(api.repairDetail + this.repair_id)
.then(response => {
console.log(response.data)
self.repair = response.data.data
self.reportinfo = this.repair
self.repairinfo = this.repair.repair
self.reporter = this.repair.user
if (self.assignee) {
self.assignee = self.repair.assigneeinfo
}
if (this.repairinfo && self.repairinfo.score) {
self.score = self.repairinfo.score
}

self.stars = self.score.score

for (var i = 0; i < self.reportinfo.image_list.length; i++) {
let image = {}
image.url = 'javascript:'
image.img = self.reportinfo.image_list[i]

self.demo01_list.push(image)
}


})
.catch(error => {
console.log(error)
})
.finally()

this.axios
.get(api.repairComment + 'object_id=' + this.repair_id + '&object_type=' + '1')
.then(response => {
console.log(response.data.data)
self.comments = response.data.data
})
.catch(error => {
console.log(error)
})
.finally()
},
data () {
return {
repair_id: '',
demo01_index: 0,
demo01_list: [],
repair: {},
rater_disabled: false,
stars: 4,
comments: [],
showAddComment: false,
default_image: 'http://lams-1257122319.cos.ap-beijing.myqcloud.com/20190301b0fbbde3f298a051cb5d464c54b018be',
reporter: {},
assignee: {},
reportinfo: {},
repairinfo: {},
score: {},
imgarrow: '',
error_msg: '',
confirm: false
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},
onSwiperItemIndexChange (index) {
console.log('demo item change', index)
},
clickAddComment () {
this.showAddComment = true
},
onHide () {
console.log('on hide')
},
onShow () {
this.$refs.confirmComment.setInputValue('')
},
onCancel () {
},
onConfirmComment (value) {
this.sendComment(value)
this.$refs.confirmComment.setInputValue('')
},
onConfirm (value) {
this.confirm = false
},
showConfirm () {
this.confirm = true
setTimeout(() => {
this.confirm = false
this.showAddComment = true
}, 3000)
},
sendComment (message) {
let self = this
if (message === '') {
this.error_msg = '评论内容不能为空'
this.showConfirm()
return
}
let posturl = api.addcomment
let fd = new FormData()
let token = window.localStorage.getItem('token')
fd.append('token', token)
fd.append('id', this.repair_id)
fd.append('type', 1)
fd.append('content', message)

self.axios
.post(posturl, fd)
.then(function (response) {
if (response.data.code === 200) {

self.comments.unshift(response.data.data)
} else if (response.data.code === 5009) {
utils.deletaUserData()
} else {

self.error_msg = response.data.code + ':' + response.data.message
self.showConfirm()
}
})
.catch(function (error) {
console.log(error)

})
.finally()

}
}
}



